div.nav-logo {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

div.nav-link {
    padding: 6px 2px;
    color: #beaeff; /*#fee6ce  #a28bff*/
    height: 45px;
    width: 160px;
    text-align: center;
    font-size: 1.6rem;
    font-family: 'LimeLight', cursive;
    text-decoration: underline;
    -webkit-text-decoration-color: transparent;
            text-decoration-color: transparent;
    border-radius: 0px !important;
    transition: text-shadow 0.4s ease-in-out, 
                color 0.4s ease-in-out, 
                -webkit-text-decoration-color 0.4s ease-in-out, 
                -webkit-transform 0.4s ease-in-out;
    transition: text-decoration-color 0.4s ease-in-out, 
                text-shadow 0.4s ease-in-out, 
                transform 0.4s ease-in-out, 
                color 0.4s ease-in-out;
    transition: text-decoration-color 0.4s ease-in-out, 
                text-shadow 0.4s ease-in-out, 
                transform 0.4s ease-in-out, 
                color 0.4s ease-in-out, 
                -webkit-text-decoration-color 0.4s ease-in-out, 
                -webkit-transform 0.4s ease-in-out;
    cursor: pointer;
}

div.nav-link:hover {
    color: #fbff00 !important;
    -webkit-text-decoration-color: #fbff00;
            text-decoration-color: #fbff00;
    text-shadow: 3px 2px 2px #2000a2;
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px);
}


div.page-logo a.logo-container {
    display: flex;
    justify-content: space-evenly;
    align-items: stretch;
    height: 57px;
    width: 205px;
    box-shadow: 10px 10px 12px 1px #0a0a0a;
}

form.form {
    margin: 70px auto;
    display: flex;
    justify-content: center;
    align-items: stretch;
    width: 100%;
    max-width: 900px;
}

form.form input.form-control {
    border-radius: 0px;
    border: none;
    background-color: #282828;
    color: white;
    transition: background-color .45s ease-in-out;
}

form.form button.btn {
