}

form.form button.btn:hover {
    background-color: #000000;
    color: white;
}

form.form input.form-control:focus, form.form button.btn:focus {
    box-shadow: 0 0 0 0 transparent;
} 


main div.query-container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    max-width: 1000px;
    margin: auto;
}

div.qcard-container {
    margin: 36px;
    border-radius: 1.5px;
    transition: box-shadow 0.5s ease-in-out, 
                background-color 0.5s ease-in-out, 
                -webkit-transform 0.5s ease-in-out;
    transition: box-shadow 0.5s ease-in-out, 
                transform 0.5s ease-in-out, 
                background-color 0.5s ease-in-out;
    transition: box-shadow 0.5s ease-in-out, 
                transform 0.5s ease-in-out, 
                background-color 0.5s ease-in-out, 
                -webkit-transform 0.5s ease-in-out;
    cursor: pointer;
}

div.qcard-container:hover {
    box-shadow: rgba(40, 40, 40, 0.4) 5px 5px, 
        rgba(40, 40, 40, 0.3) 10px 10px, 
        rgba(40, 40, 40, 0.2) 15px 15px, 
        rgba(40, 40, 40, 0.1) 20px 20px, 
        rgba(40, 40, 40, 0.05) 25px 25px;
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px);
    background-color: #0a0a14;
}

div.qcard-container:hover div.qcard-image-container {
    box-shadow: inset 0px -20px 95px 11px black, 
                0px 0px 100px 10px black;
    transition: box-shadow 1.35s ease-in-out;
}

div.qcard-container:hover div.q-card-img-container{
    /*background-size: 115% !important;*/
    -webkit-transform: scale(1.18);
            transform: scale(1.18);
    opacity: 0.7;
    -webkit-filter: blur(1.45px);
            filter: blur(1.45px);
    transition: opacity 5s ease-in-out,
                -webkit-transform 5s ease-in-out, 
                -webkit-filter 3.2s cubic-bezier(0.61, 0, 1, 0.43);
    transition: transform 5s ease-in-out, 
                opacity 5s ease-in-out,
                filter 3.2s cubic-bezier(0.61, 0, 1, 0.43);
    transition: transform 5s ease-in-out, 
                opacity 5s ease-in-out,
                filter 3.2s cubic-bezier(0.61, 0, 1, 0.43), 
                -webkit-transform 5s ease-in-out, 
                -webkit-filter 3.2s cubic-bezier(0.61, 0, 1, 0.43);
}

div.qcard-container:hover div.qcard-title {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-text-decoration-color: #ffffff;
            text-decoration-color: #ffffff;
    transition: -webkit-transform 1.4s ease-in-out 0.6s, 
                -webkit-text-decoration-color 0.4s ease-in-out 1.6s;
    transition: transform 1.4s ease-in-out 0.6s, 
                text-decoration-color 0.4s ease-in-out 1.6s;
    transition: transform 1.4s ease-in-out 0.6s, 
                text-decoration-color 0.4s ease-in-out 1.6s, 
                -webkit-transform 1.4s ease-in-out 0.6s, 
                -webkit-text-decoration-color 0.4s ease-in-out 1.6s;
}

div.qcard-box {
    height: 300px;
    width: 200px;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

div.qcard-image-container {
    width: 200px;
    height: 300px;
    border-radius: 1.5px;
    position: absolute;
    overflow: hidden;
/*    background-position: center top !important;
    background-repeat: no-repeat;
    background-size: 100% !important;*/
    transition: box-shadow 1.7s ease-in-out;
}

div.q-card-img-container {
    /*display: flex;
    justify-content: center;
    align-items: center;*/
    width: 100%;
    height: 100%;
    -webkit-filter: blur(0);
            filter: blur(0);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
    transition: opacity 1.7s ease-in-out,
                -webkit-transform 1.7s ease-in-out, 
                -webkit-filter 1.65s ease-in-out;
    transition: transform 1.7s ease-in-out, 
                opacity 1.7s ease-in-out,
                filter 1.65s ease-in-out;
    transition: transform 1.7s ease-in-out, 
                opacity 1.7s ease-in-out,
                filter 1.65s ease-in-out, 
                -webkit-transform 1.7s ease-in-out, 
                -webkit-filter 1.65s ease-in-out;
}

