div.qcard-title {
    font-size: 1.775rem;
    font-family: 'Francois One';
    font-style: italic;
    /*font-weight: bold;*/
    text-decoration: underline;
    -webkit-text-decoration-color: transparent;
            text-decoration-color: transparent;
    text-decoration-thickness: 3px;
    padding: 4px 9px;
    margin: 3px;
    max-width: 200px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #ffffff;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
    transition: -webkit-transform 0.54s ease-in-out, 
                -webkit-text-decoration-color 0.35s ease-in-out;
    transition: transform 0.54s ease-in-out, 
                text-decoration-color 0.35s ease-in-out;
    transition: transform 0.54s ease-in-out, 
                text-decoration-color 0.35s ease-in-out, 
                -webkit-transform 0.54s ease-in-out, 
                -webkit-text-decoration-color 0.35s ease-in-out;
}

div.next-page-card {
    font-size: 1.6rem;
    margin: 36px;
    border-radius: 1.5px;
    height: 300px;
    width: 200px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(40, 40, 40, 0%);
}

div.next-page-card div.nextpage-link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    flex-grow: 1;
    text-decoration: underline;
    -webkit-text-decoration-color: transparent;
            text-decoration-color: transparent;
    cursor: pointer;
    transition: -webkit-text-decoration-color 0.15s ease-in-out;
    transition: text-decoration-color 0.15s ease-in-out;
    transition: text-decoration-color 0.15s ease-in-out, -webkit-text-decoration-color 0.15s ease-in-out;
}

div.next-page-card div.nextpage-link:hover {
    -webkit-text-decoration-color: white;
            text-decoration-color: white;
}

div.next-page-card div.nextpage-link:hover div {
    margin-left: 12px;
}

div.next-page-card div.nextpage-link > div {
    height: 30px;
    margin-left: 10px;
    padding-bottom: 40px;
    transition: margin-left 0.15s ease-in-out;
}

div.next-page-card div.nextpage-link svg {
    height:  30px;
    fill: white;
}




@media only screen and (max-width: 700px) {
    div.nav-logo {
        justify-content: space-around;
    }

    div.nav-link {
        width: 30px;
        display: flex;
        align-items: center;
    }

    div.nav-link:hover {
        -webkit-transform: translate(0px);
                transform: translate(0px);
    }

    span.nav-link-text {
        display: none;
    }

    .nav-link-icon {
        display: block !important;
        color: #beaeff !important;
        font-size: 2rem;
        transition: color 0.35s ease; 
    }

    .nav-link-icon:hover {
        color: #fbff00 !important;
    }

    .nav-link-icon svg {font-size: 2rem;}
}

@media only screen and (min-width: 700px) {
    .nav-link-icon {
        display: none !important;    
    }
    
}

@media only screen and (max-width: 822px) {
    div.next-page-card {
        display: none;
    }
}


