.movie-container, .tv-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow: hidden;
}

.movie-title, .tv-title {
	font-family: 'C Font8';
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	max-width: 800px;
	margin-bottom: 30px;
	margin-top: 30px;
	padding: 20px;
/*	overflow: hidden;*/
}

.movie-title h1, .tv-title h1 {font-family: 'C Font6'; text-align: center;}
.movie-title h5, .tv-title h5 {font-size: calc(0.775rem + 0.65vw);}

.movie-title span, .tv-title span {
	margin-left: 10px; 
	margin-right: 10px;
	color: #9e9e9e;
}

.movie-title .ttSeperator, .tv-title .ttSeperator {
	height: 30px;
	width: 2px;
	border-radius: 2px;
	background: #9e9e9e;
	display: inline-block;
	vertical-align: -8px;
}

.backdrop-container {
	display: flex;
	justify-content: center;
	padding: 20px;
}

.backdrop-container img {
	min-width: 700px;
	max-width: 100%;
	width: calc(100vw - 40px);
}

.movie-poster-overview, .tv-poster-overview {
	display: flex;
	padding: 20px;
}

.movie-Poster, .tv-Poster {
	width: 200px;
}

.movie-overview, .tv-overview {
	margin-left: calc(20px + 1.4vw);
	max-width: 600px;
	display: flex;
	align-items: flex-start;
	overflow-y: hidden;
	text-overflow: ellipsis;
}

.movie-overview p, .tv-overview p {
	text-align: center;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 9;
	display: -webkit-box;
	overflow: hidden;
	word-break: break-word;
	max-height: calc(300px - 1rem);
}

.lime-headings {
	font-family: 'Limelight';
	text-align: center;
}

.lime-headings:not(.part) {margin-top: 50px;}

#line-of-break {
	background: linear-gradient(90deg, transparent, gray, transparent);
    height: 2px;
    margin-top: 24px;
}

#catch-phrase {
	font-family: 'Dancing Script';
	letter-spacing: 0.1rem;
	font-size: 1.2rem;
}
#catch-phrase:before {content: open-quote;}
#catch-phrase:after {content: close-quote;}


.movie-credits, .tv-credits {
	width: 100%;
	padding: 20px;
	display: flex;
	justify-content: space-around;
}

.movie-casts, .tv-casts {
	display: grid;
	grid-template-columns: calc(140px + 6vw) calc(140px + 6vw) calc(140px + 6vw);
	justify-content: space-around;
	max-height: 820px;
	overflow-y: auto;
}

.movie-cast-container, .tv-cast-container {
	margin: 16px;
	height: 240px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.cast-photo-backup {
	height: 180px;
	width: 120px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}

