.no-show {
	opacity: .35;
    cursor: auto;
    pointer-events: none;
}

.swiper-button-prev.csl:not(.no-show):hover, 
.swiper-button-next.csl:not(.no-show):hover {
	background-color: #0a0c1880;
}

.carousel.v-c {
	margin-top: 40px;
    width: 80vw;
    min-width: 600px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: block;
    height: 320px;
    overflow: hidden;
}

.carousel-container {
	display: flex;
	align-items: center;
	transition-property: -webkit-transform;
	transition-property: transform;
	transition-property: transform, -webkit-transform;
	transition-duration: 700ms;
}

.iframe-body {
	height: 315px;
	min-width: 560px;
	flex-shrink: 0;
	margin-left: calc(calc(40vw - 280px) * 0.5);
}

.iframe-body:first-child {
	margin-left: calc(40vw - 280px);
}

.iframe-body:last-child {
	margin-right: calc(40vw - 280px);
}


/*webkit scrollbar*/
.credit-info-container::-webkit-scrollbar,
.movie-casts::-webkit-scrollbar,
.tv-casts::-webkit-scrollbar {
    width: 5px;
}

/* Track */
.credit-info-container::-webkit-scrollbar-track,
.movie-casts::-webkit-scrollbar-track,
.tv-casts::-webkit-scrollbar-track {
    background-color: #0a0c18;
    cursor: pointer !important;
    -webkit-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out;
}

/* Handle */
.credit-info-container::-webkit-scrollbar-thumb,
.movie-casts::-webkit-scrollbar-thumb,
.tv-casts::-webkit-scrollbar-thumb {
    background: #282828; 
    border-radius: 0px;
    -webkit-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out;
    cursor: pointer !important;
}

/* Handle inactive */
.credit-info-container::-webkit-scrollbar-thumb:window-inactive,
.movie-casts::-webkit-scrollbar-thumb:window-inactive,
.tv-casts::-webkit-scrollbar-thumb:window-inactive {
    background-color: rgba(0, 0, 0, 0.6) !important;
}


@media only screen and (max-width: 1000px) {
	.movie-casts, .tv-casts {
		display: grid;
		grid-template-columns: calc(140px + 6vw) calc(140px + 6vw);
		justify-content: space-around;
	}

	.vertical-divider {
		height: 770px;
	}
}

@media only screen and (max-width: 1200px) {
	.movie-casts, .tv-casts {
		display: grid;
		grid-template-columns: calc(140px + 6vw) calc(140px + 6vw) calc(140px + 6vw);
		justify-content: space-around;
	}

	.movie-credits, .tv-credits {
		flex-direction: column;
		align-items: center;
	}

	.credit-info-container {
		width: 90vw;
		max-height: 10000px !important;
		margin-top: 20px;
	}

	.credit-movies {justify-content: space-evenly;}
}


